'use client'

import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { ArrowLeft } from "lucide-react";
import Link from "next/link";
import { 
  CustomAwardIcon, 
  CustomGlobeIcon, 
  CustomTargetIcon,
  CustomBrainIcon,
  CustomCpuIcon
} from "@/components/icons/custom-icons";
import { useScrollAnimation } from "@/hooks/use-scroll-animation";

export default function AboutPage() {
  const valuesAnimation = useScrollAnimation({ threshold: 0.2 });

  return (
    <div className="min-h-screen bg-gradient-to-br from-background/50 to-muted/10">
      {/* Header */}
      <section className="pt-28 pb-24">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <Link href="/">
            <Button
              variant="ghost"
              className="mb-8 hover-elevate"
              data-testid="button-back-about"
            >
              <ArrowLeft className="w-4 h-4 mr-2" />
              Back to Home
            </Button>
          </Link>
          
          <div className="text-center space-y-6 mb-16 animate-fade-in-up">
            <Badge variant="outline" className="border-primary/30 text-primary font-picabord">
              About PICABORD
            </Badge>
            <h1 className="text-4xl md:text-6xl font-bold">
              Pioneering{" "}
              <span className="text-primary">
                Innovation
              </span>
            </h1>
            <p className="text-xl text-muted-foreground max-w-3xl mx-auto">
              A unique blend of software and hardware innovation, we bridge the digital and physical worlds 
              to create comprehensive technology solutions that drive real-world impact.
            </p>
          </div>

        </div>
      </section>

      {/* Software + Hardware Focus */}
      <section className="py-16 bg-gradient-to-r from-muted/10 to-background">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div 
            ref={valuesAnimation.ref as React.RefObject<HTMLDivElement>}
            className={`max-w-4xl mx-auto text-center mb-16 transition-all duration-700 ${
              valuesAnimation.isVisible 
                ? 'opacity-100 translate-y-0' 
                : 'opacity-0 translate-y-8'
            }`}
          >
            <h2 className="text-3xl md:text-4xl font-bold mb-6">
              Where Software Meets{" "}
              <span className="text-primary">Hardware</span>
            </h2>
            <p className="text-lg text-muted-foreground leading-relaxed">
              At <span className="font-picabord text-foreground">PICABORD</span>, we don't just build software or hardware—we create complete ecosystems. 
              Our dual expertise allows us to design integrated solutions that seamlessly combine the power of 
              advanced software with precision-engineered hardware, delivering unmatched performance and reliability.
            </p>
          </div>

          <div className="grid lg:grid-cols-2 gap-8 mb-16">
            <Card 
              className={`p-8 hover-elevate transition-all duration-700 ${
                valuesAnimation.isVisible 
                  ? 'opacity-100 translate-x-0' 
                  : 'opacity-0 -translate-x-8'
              }`}
            >
              <CardContent className="p-0">
                <div className="flex items-start space-x-4">
                  <div className="w-12 h-12 bg-gradient-to-r from-chart-2 to-primary rounded-lg flex items-center justify-center flex-shrink-0">
                    <CustomBrainIcon size={24} className="text-primary-foreground" />
                  </div>
                  <div>
                    <h3 className="text-xl font-semibold mb-3">Software Excellence</h3>
                    <p className="text-muted-foreground leading-relaxed">
                      Our TEC division crafts innovative software solutions—from enterprise applications 
                      to cloud platforms—that transform how businesses operate and scale in the digital age.
                    </p>
                  </div>
                </div>
              </CardContent>
            </Card>

            <Card 
              className={`p-8 hover-elevate transition-all duration-700 delay-150 ${
                valuesAnimation.isVisible 
                  ? 'opacity-100 translate-x-0' 
                  : 'opacity-0 translate-x-8'
              }`}
            >
              <CardContent className="p-0">
                <div className="flex items-start space-x-4">
                  <div className="w-12 h-12 bg-gradient-to-r from-chart-1 to-primary rounded-lg flex items-center justify-center flex-shrink-0">
                    <CustomCpuIcon size={24} className="text-primary-foreground" />
                  </div>
                  <div>
                    <h3 className="text-xl font-semibold mb-3">Hardware Innovation</h3>
                    <p className="text-muted-foreground leading-relaxed">
                      Our PIKA division engineers cutting-edge embedded systems and single-board computers 
                      that power the next generation of IoT, edge computing, and industrial applications.
                    </p>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          <div className="grid lg:grid-cols-3 gap-8">
            {[
              { icon: CustomTargetIcon, title: "Mission", description: "To revolutionize technology through innovative solutions that empower businesses and transform industries worldwide.", gradient: "from-chart-1 to-primary" },
              { icon: CustomGlobeIcon, title: "Vision", description: "To be the global leader in advanced technology solutions, creating a connected world where innovation drives human progress.", gradient: "from-primary to-chart-2" },
              { icon: CustomAwardIcon, title: "Values", description: "Excellence, innovation, integrity, and collaboration guide every decision we make and every solution we deliver.", gradient: "from-chart-2 to-chart-1" }
            ].map((item, index) => (
              <Card 
                key={index}
                className={`p-8 text-center hover-elevate transition-all duration-700 ${
                  valuesAnimation.isVisible 
                    ? 'opacity-100 translate-y-0' 
                    : 'opacity-0 translate-y-8'
                }`}
                style={{ transitionDelay: `${(index + 2) * 150}ms` }}
              >
                <CardContent className="p-0">
                  <div className={`w-16 h-16 bg-gradient-to-r ${item.gradient} rounded-full flex items-center justify-center mx-auto mb-4`}>
                    <item.icon size={32} className="text-primary-foreground" />
                  </div>
                  <h3 className="text-xl font-bold mb-4">{item.title}</h3>
                  <p className="text-muted-foreground">{item.description}</p>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
}
